import React, {useEffect} from 'react';
import {useLocation} from "react-router-dom";

function Naver(props) {
    const location = useLocation();
    const NaverLogin = () => {
        const {naver} = window
        const naverLogin = new naver.LoginWithNaverId(
            {
                clientId: "AmX1zvlO8VFKoSiQZoaz",
                callbackUrl: "http://localhost:3000/login/ncallback",
                isPopup: false, /*    , true   */
                loginButton: {color: "green", type: 3, height: 47}, /*     */
                //callbackHandle: true,
            }
        );
        naverLogin.init();
        console.log(naverLogin);
        if (!location.hash) return;
        const token = location.hash.split('=')[1].split('&')[0]; //token 
        console.log(token);
    }

    useEffect(() => {
        NaverLogin()
    }, []);
    return (
        <div className={'grid-naver'} id={'naverIdLogin'}>
            2
        </div>
    );
}

export default Naver;